import React, { useState } from 'react';
import { View, TouchableOpacity, StyleSheet, Animated, Dimensions, Text } from 'react-native';

const columns = 7;
const rows = 6;
const windowHeight = Dimensions.get('window').height;
let windowWidth = Dimensions.get('window').width;
if (windowHeight < windowWidth) { windowWidth = windowHeight; }
const slotMargin = 5;
const boardPadding = 10;
const slotSize = (windowWidth - 2 * (columns * slotMargin + boardPadding)) / columns;

const CustomAlert = ({ title, onDismiss }) => {
  return (
    <View style={styles.alertContainer}>
      <Text style={styles.alertTitle}>{title}</Text>
      <TouchableOpacity style={styles.alertButton} onPress={onDismiss}>
        <Text style={styles.alertButtonText}>Play again</Text>
      </TouchableOpacity>
    </View>
  );
};
// Single/Multiplayer mode
// Tap top text
// Exit Game

const Board = () => {
  const emptyBoard = Array.from({ length: columns }, () => Array.from({ length: rows }, () => null));
  const [board, setBoard] = useState(emptyBoard);
  const [currentPlayer, setCurrentPlayer] = useState('red');
  const [animations, setAnimations] = useState(emptyBoard.map(col => col.map(() => new Animated.Value(0))));
  const [animating, setAnimation] = useState(false);
  const [winner, setWinner] = useState(null);
  const [draw, setDraw] = useState(false);

  const dismissAlert = () => {
    setWinner(null);
    setDraw(false);
    clearBoard();
  };

  const clearBoard = () => {
    setBoard(emptyBoard);
    setAnimations(emptyBoard.map(col => col.map(() => new Animated.Value(0))));
    setCurrentPlayer('red');
  };

  const checkForWin = (board) => {
    for (let r = 0; r < rows; r++) {
      for (let c = 0; c < columns - 3; c++) {
        if (board[c][r] && board[c][r] === board[c + 1][r] && board[c][r] === board[c + 2][r] && board[c][r] === board[c + 3][r]) {
          return board[c][r];
        }
      }
    }

    for (let c = 0; c < columns; c++) {
      for (let r = 0; r < rows - 3; r++) {
        if (board[c][r] && board[c][r] === board[c][r + 1] && board[c][r] === board[c][r + 2] && board[c][r] === board[c][r + 3]) {
          return board[c][r];
        }
      }
    }

    for (let r = 0; r < rows - 3; r++) {
      for (let c = 0; c < columns - 3; c++) {
        if (board[c][r] && board[c][r] === board[c + 1][r + 1] && board[c][r] === board[c + 2][r + 2] && board[c][r] === board[c + 3][r + 3]) {
          return board[c][r];
        }
      }
    }

    for (let r = 0; r < rows - 3; r++) {
      for (let c = 3; c < columns; c++) {
        if (board[c][r] && board[c][r] === board[c - 1][r + 1] && board[c][r] === board[c - 2][r + 2] && board[c][r] === board[c - 3][r + 3]) {
          return board[c][r];
        }
      }
    }

    return null;
  };

  const checkForDraw = (board) => {
    let emptyCells = 0
    for (let r = 0; r < rows; r++) {
      for (let c = 0; c < columns; c++) {
        if(board[c][r] === null) { emptyCells += 1; }
      }
    }
    console.log(emptyCells);
    return (emptyCells === 0)
  };

  const handlePress = (colIndex) => {
    if (animating) return;

    setAnimation(true);

    const newBoard = board.map(column => [...column]);
    const column = newBoard[colIndex];
    const rowIndex = column.lastIndexOf(null);
    if (rowIndex === -1) {
      setAnimation(false);
      return;
    }
    column[rowIndex] = currentPlayer;
    setBoard(newBoard);

    Animated.timing(animations[colIndex][rowIndex], {
      toValue: 1,
      duration: 500,
      useNativeDriver: true,
    }).start(() => {
      setAnimations(prev => prev.map(column => column.map(animation => animation)));
      setAnimation(false);

      const winnerColor = checkForWin(newBoard);
      if (winnerColor) {
        setWinner(winnerColor);
        return;
      }

      if(checkForDraw(newBoard)) {
        setWinner('white');
        setDraw(true);
        return;
      }
      
      setCurrentPlayer(currentPlayer === 'red' ? 'yellow' : 'red');
    });
  };

  return (
    <View style={styles.container}>
      <Text style={[styles.turnText, { color: winner ? winner : currentPlayer === 'red' ? 'red' : 'yellow', display: draw ? 'none':'block'}]}>
        {winner ? `${currentPlayer.charAt(0).toUpperCase() + currentPlayer.slice(1)} wins` : `${currentPlayer.charAt(0).toUpperCase() + currentPlayer.slice(1)}'s turn`}
      </Text>
      <Text style={[styles.turnText, { color: 'white', display: draw ? 'block':'none'}]}>
        {`Draw!`}
      </Text>
      <View style={[styles.board, { width: windowWidth }]}>
        {board[0].map((cell, rowIndex) => (
          <View key={rowIndex} style={styles.row}>
            {board.map((col, colIndex) => (
              <TouchableOpacity
                key={`${colIndex}-${rowIndex}`}
                style={styles.slot}
                onPress={() => handlePress(colIndex)}
                disabled={animating}
              >
                <Animated.View
                  style={[
                    board[colIndex][rowIndex] === 'red' ? styles.red : board[colIndex][rowIndex] === 'yellow' ? styles.yellow : null,
                    {
                      transform: [{
                        translateY: animations[colIndex][rowIndex].interpolate({
                          inputRange: [0, 1],
                          outputRange: [-(slotSize / 0.8) * (rowIndex + 1), 0],
                        })
                      }]
                    },
                  ]}
                />
              </TouchableOpacity>
            ))}
          </View>
        ))}
        {(winner || draw) && (
          <CustomAlert
            title={`Well played ðŸŽ‰`}
            onDismiss={dismissAlert}
          />
        )}
      </View>
    </View>
  );
};


const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },

  turnText: {
    fontWeight: 'bold',
    fontSize: 24,
    textAlign: 'center',
    backgroundColor: '#037',
    width: windowWidth,
  },

  board: {
    padding: boardPadding,
    backgroundColor: '#037',
    border: 0,
  },
  row: {
    flexDirection: 'row',
  },
  slot: {
    width: slotSize,
    height: slotSize,
    borderRadius: slotSize / 2,
    borderColor: '#fff',
    borderWidth: 2,
    alignItems: 'center',
    justifyContent: 'center',
    margin: slotMargin,
    backgroundColor: '#eef',
  },
  red: {
    width: slotSize * 0.8,
    height: slotSize * 0.8,
    borderRadius: (slotSize * 0.8) / 2,
    backgroundColor: 'red',
  },
  yellow: {
    width: slotSize * 0.8,
    height: slotSize * 0.8,
    borderRadius: (slotSize * 0.8) / 2,
    backgroundColor: 'yellow',
  },

  alertContainer: {
    position: 'absolute',
    top: 0,
    height: slotSize * rows + 2 * (boardPadding + slotMargin * rows),
    left: 0,
    right: 0,
    padding: 20,
    backgroundColor: 'white',
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: 'rgba(255, 255, 255, 0.8)'
  },
  alertTitle: {
    fontWeight: 'bold',
    fontSize: 28,
    marginBottom: 10,
  },
  alertButton: {
    backgroundColor: '#0275d8',
    padding: 10,
    borderRadius: 5,
  },
  alertButtonText: {
    color: 'white',
    fontSize: 24,
  },
});

export default Board;
